import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { CpsService } from '../../services/cps.service';
import { Router } from '@angular/router';



@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.css']
})
export class AddItemComponent {

  addItemForm: FormGroup;

  constructor(private formBuilder: FormBuilder,
            private cpsService: CpsService,
            private router: Router) {

    this.addItemForm = formBuilder.group({
      'itemCategory': ['', [Validators.required]],
      'itemSubCategory': ['', [Validators.required]],
      'itemName': ['', [Validators.required]],
      'itemDescription': ['', [Validators.required]],
      'itemVariables': formBuilder.array([['', Validators.required]]),
      'itemVariablesValues': formBuilder.array([['', Validators.required]])
    })


  }

  removeVariable(index) {

    (<FormArray>this.addItemForm.controls['itemVariables']).removeAt(index);
    (<FormArray>this.addItemForm.controls['itemVariablesValues']).removeAt(index);

  }

  addVariable() {

    (<FormArray>this.addItemForm.controls['itemVariables']).push(new FormControl('', Validators.required));
    (<FormArray>this.addItemForm.controls['itemVariablesValues']).push(new FormControl('', Validators.required));

  }

  onSubmit() {

    //format the data to db model
    let newItemData = this.addItemForm.value;
    //form variable array object
    if (newItemData.itemVariables.length) {
      let variableObjectArray = [];
      for (let i = 0; i < newItemData.itemVariables.length; i++) {
        variableObjectArray.push({ "variableName": newItemData.itemVariables[i], "variableValues": newItemData.itemVariablesValues[0].split(',') })
      }
      newItemData.variables = variableObjectArray;
    }

    delete newItemData.itemVariables;
    delete newItemData.itemVariablesValues;
    //temporary item ID, server will replace this with proper ID
    newItemData.itemId = "temp";
    console.log(newItemData);


    //post the data to the server
    this.cpsService.saveItem(newItemData).subscribe(response=>{
      console.log("response: " + JSON.stringify(response));
      this.router.navigateByUrl('/');
    });

    

  }



}



